import React from 'react';
import { Page1, Page2, Page3 } from './pages'

function pageLoad(props) {
    console.log(props);
    if (props === 1)
        return Page1;
    else if (props === 2)
        return Page2;
    else if (props === 3)
        return Page3;
}
class Main extends React.Component {


    render() {
        return (
            <div> {pageLoad(this.props.pageNum)}</div>
        );
    }
}

export default Main;


