import React from 'react';
import './styles.css';

class Navbar extends React.Component {

    constructor() {
        super();
        this.state = {
            page: 1,
        }
        this.changePage = this.changePage.bind(this);
    };

    changePage(props) {
        this.props.onSelectTab(props);
    }

    render() {
        return (
            <div className="navbar">
                <ul>
                    <li onClick={() => this.changePage(1)}><h3>Tier List</h3></li>
                    <li onClick={() => this.changePage(2)}><h3>Releases</h3></li>
                    <li onClick={() => this.changePage(3)}><h3>Chronologies</h3></li>
                    <li><a href="https://pastebin.com/raw/zDgawtNb"><h3>Download</h3></a></li>
                    <li><a href="https://darklegionsarchive.neocities.org/metal/"><h3>Dark Legions Archive</h3></a></li>
                    <li><a href="https://discord.gg/T2Ay6B9"><h3>Chatroom</h3></a></li>
                </ul>
            </div>
        )
    }
}

export default Navbar;


